.logos-container {
  margin: 20px 0 40px 0;
  text-align: center;
  svg {
    display: inline-block;
  }
}

.devextreme-logo {
  width: 80px;
  height: 15px;
  margin-bottom: 10px;
}

.react-logo {
  width: 80px;
  height: 30px;
}

.plus {
  margin: 10px 10px;
  width: 10px;
  height: 10px;
}

.screen-x-small .logos-container {
  svg {
    width: 100%;
    display: block;
    &.plus {
      margin: 0;
    }
  }
}
