* {box-sizing: border-box;}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.topnav {
  overflow: hidden;
  background-color: #e9e9e9;
}

.topnav a {
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 10px 16px;
  text-decoration: none;
  font-size: 14px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #52728c;
  color: white;
}

.topnav .search-container {
  float: right;
}

.topnav input[type=text] {
  padding: 6px;
  margin-top: 8px;
  font-size: 14px;
  border: none;
}

.topnav .search-container button {
  float: right;
  padding: 6px 10px;
  margin-top: 8px;
  margin-right: 16px;
  background: #ddd;
  font-size: 14px;
  border: none;
  cursor: pointer;
}

.topnav .search-container button:hover {
  background: #ccc;
}

@media screen and (max-width: 600px) {
  .topnav .search-container {
    float: none;
  }
  .topnav a,.dx-field, .topnav input[type=text], .topnav .search-container button {
    float: none;
    display: block;
    text-align: left;
    width: 100%;
    margin: 0;
    padding: 7px;
  }
  .topnav input[type=text] {
    border: 1px solid #ccc;  
  }
  
}


.dx-texteditor.dx-editor-filled .dx-texteditor-input,
.dx-texteditor.dx-editor-outlined .dx-placeholder::before,
.dx-texteditor.dx-editor-outlined .dx-texteditor-input {
  padding: 9px 16px 9px;
}

.dx-texteditor.dx-editor-filled.dx-state-disabled .dx-texteditor-input,
.dx-texteditor.dx-editor-filled.dx-state-readonly .dx-texteditor-input,
.dx-texteditor.dx-editor-filled.dx-state-readonly.dx-state-hover .dx-texteditor-input {
  color: rgba(0, 0, 0, 0.896);
}
.dx-texteditor.dx-editor-underlined {
  background-color: transparent;
  padding: 0;
}
.dx-toolbar .dx-toolbar-items-container {
  height: 40px;
  overflow: visible;
}
.dx-toolbar .dx-toolbar-menu-container {
  padding: 0;
}
.dx-popup-title.dx-toolbar .dx-toolbar-items-container {
  height: 40px;
  overflow: visible;
}
.dx-popup-title.dx-toolbar .dx-toolbar-menu-container {
  padding: 0;
}
.dx-popup-title.dx-toolbar .dx-toolbar-label {
  font-size: 13px;
  font-weight: 500;
}
.dx-popup-title.dx-toolbar .dx-button.dx-closebutton {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 13px;
}
.dx-lookup-field::before {
  content: ".";
}
.dx-lookup.dx-editor-filled,
.dx-lookup.dx-editor-outlined {
  height: 38px;
}
.dx-lookup.dx-editor-filled .dx-lookup-field,
.dx-lookup.dx-editor-outlined .dx-lookup-field {
  padding: 14px 35px 14px 14px;
}
.dx-datagrid .dx-row > td {
  padding: 8px 13px;
  font-size: 13px;
  line-height: 14px;
}

.dx-datagrid .dx-datagrid-table .dx-header-row > td {
  padding-top: 14px;
  padding-bottom: 14px;
}
.dx-datagrid .dx-datagrid-content .dx-datagrid-table .dx-row > td.dx-cell-modified:not(.dx-field-item-content):not(.dx-validation-pending),
.dx-datagrid .dx-datagrid-content .dx-datagrid-table .dx-row > td.dx-datagrid-invalid:not(.dx-field-item-content):not(.dx-validation-pending):not(.dx-datagrid-select-all),
input.dx-hidden {
  margin-right: 16px;
}
.dx-context-menu .dx-menu-items-container {
  padding: 4px 0;
}
.dx-field-item-label-location-top {
  display: block;
  padding: 8px 16px 3px;
}

.dx-layout-manager .dx-field-item:not(.dx-last-col) {
    padding-right: 20px;
}
.dx-layout-manager .dx-field-item {
    padding-bottom: 5px;
    font-size: 13px;
}
.dx-datagrid-edit-popup-form {
  padding: 15px;
}

.dx-field {
  margin: 0 0 0px;
}

.mg-field-label {
  float: left;
  width: 10%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.dx-texteditor-input {
  -webkit-appearance: none;
  width: 100%;
  height: 100%;
  outline: 0;
  border: 0;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
  margin: 0;
  background-color: transparent;
  color: rgba(0,0,0,.87);
  font-size: 13px;
}

.dx-datagrid-headers {
  color: rgba(0,0,0,.87) !important;
  background-color:  #e6beb3 !important;
}

html, body {
  overscroll-behavior: none;
}

.options {
  margin-top: 20px;
  padding: 20px;
  background-color: rgba(191, 191, 191, 0.15);
  position: relative;
}

.caption {
  font-size: 18px;
  font-weight: 500;
}

.option-container {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
}

.option {
  margin-top: 10px;
  display: flex;
  align-items: center;
}

.option-caption {
  white-space: nowrap;
  margin: 0 8px;
}

.option.check-box {
  width: 150px;
  display: flex;
}

.option > .select {
  width: 155px;
  display: flex;
}


html, body {
  overscroll-behavior: none;
}

.dx-searchbox.dx-editor-filled .dx-icon-search,
.dx-searchbox.dx-editor-outlined .dx-icon-search {
  position: absolute;
  top: 50%;
  margin-top: -23px;
  width: 32px;
  height: 46px;
  background-position: 12px 12px;
  background-size: 22px 22px;
  padding: 12px 8px 12px 1px;
  font-size: 22px;
  text-align: center;
  line-height: 22px;
}

.dx-texteditor.dx-editor-filled .dx-placeholder::before,
.dx-texteditor.dx-editor-filled .dx-texteditor-input,
.dx-texteditor.dx-editor-outlined .dx-placeholder::before,
.dx-texteditor.dx-editor-outlined .dx-texteditor-input {
  padding: 15px 16px 14px 16px;
}

